import tagGenderAge from './tag-gender-age.vue';
export default {
    props:{
        noJump:{
            type:Boolean,
            default:false
        },
        item:Object,
        index:Number           
    },
    components:{
        tagGenderAge
    },
    data() {
        return {
            isFollow: this.item.isFollow
        }
    },
    methods:{
        openDetail(){
            uni.navigateTo({
                url:'../../pages/detail/detail?detailData='+JSON.stringify(this.item)
            })
        },
       //关注
       async follow(){
           try{
               let [err,res] = await this.$http.post('follow',{
                   follow_id:this.item.userid
               },{
                   token:true,
                   checkToken:true,
                   checkAuth:true
               });
           // 错误处理
               if (!this.$http.errorCheck(err,res)){
                   return;
               }
           // 通知首页修改数据
               uni.showToast({ title: '关注成功' });
               let resdata = {
                   type:"guanzhu",
                   userid:this.item.userid,
                   data:true
               };
           // 通知父组件
               this.$emit('changeevent',resdata);
           // 通知首页
               uni.$emit('updateData',resdata);    
           }catch(e){
               //TODO handle the exception
           }
       },
       async likeOpration(likeStat){
           let index = 1 ; // 操作后的状态
           if(this.item.likeInfo.index === index) return; // 状态相同不修改
           let [err,res] = await this.$http.post('support',{
               post_id:this.item.id,
               type:index-1
           },{
               token:true,
               checkToken:true,
               checkAuth:true
           });
           // 错误处理
           if (!this.$http.errorCheck(err,res)) return;
           uni.showToast({
               title: index == 1 ? "顶+1" : "踩+1"
           });
           let resdata = {
               type:"support",
               post_id:this.item.id,
               do:index
           };
           // 通知父组件
           this.$emit('changeevent',resdata);
           // 通知全局
           uni.$emit("updateData",resdata);
           return;
       },
       openSpace(){
           if(this.noJump)  return;
           uni.navigateTo({
               url:'../../pages/user-space/user-space?userid='+this.item.userid
           })
       }
    }
}
